article {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  width: 100%;
  line-height: 1.1;
  font-weight: lighter;
  a {
    text-decoration: none;
    color: inherit;
    &:visited {
      color: inherit;
    }
  }
}
.colored {
  color: cyan;
}
.colored2 {
  color: magenta;
}


