@keyframes rotate {
  from{ transform: rotateY(0deg) rotateX(0deg) rotateZ(0deg);  }
  to{ transform:rotateZ(360deg) rotateY(-180deg) rotateX(90deg); }
}
@keyframes rotateSmall {
  from {transform: rotatey(0deg) rotateX(0deg)  }
  to{transform: rotateX(-90deg) rotateY(-90deg) rotateZ(-180deg) ;
  }
}

.cubeWrap {
  height: 300px;
  width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;

}

.cubecontainer {
  display: flex;
  justify-content: center;

}

.container {
  width: 150px;
  height: 150px;
  position: absolute;
  perspective: 10000px;

  display: flex;
  justify-content: center;
  align-items: center;

}
.containerSmall {
  width: 50px;
  height: 50px;
  position: absolute;
  perspective: 10000px;
  display: flex;
  justify-content: center;
  align-items: center;

}

.cube {
  width: 100%;
  height: 100%;
  position: absolute;
  transform-style: preserve-3d;
  perspective: 10000px;
  transition: all .2s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;
  animation: rotate 10s linear infinite;
}
.cubeSmall {
  width: 100%;
  height: 100%;
  position: absolute;
  transform-style: preserve-3d;
  perspective: 10000px;
  transition: all .2s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: rotateSmall 2s linear infinite;
}

.cube figure {
  margin: 0;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  border: 2px solid #444;
  transition: all .5s ease;
  box-sizing: border-box;
}
.cubeSmall figure {
  margin: 0;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  border: 2px solid #444;
  transition: all .2s ease;
  box-sizing: border-box;
}


.cube .front  { transform: rotateY(   0deg ) translateZ( 75px ); }
.cube .back   { transform: rotateX( 180deg ) translateZ( 75px ); }
.cube .right  { transform: rotateY(  90deg ) translateZ( 75px ); }
.cube .left   { transform: rotateY( -90deg ) translateZ( 75px ); }
.cube .top    { transform: rotateX(  90deg ) translateZ( 75px ); }
.cube .bottom { transform: rotateX( -90deg ) translateZ( 75px ); }

.cubeSmall .front  { transform: rotateY(   0deg ) translateZ( 25px ); }
.cubeSmall .back   { transform: rotateX( 180deg ) translateZ( 25px ); }
.cubeSmall .right  { transform: rotateY(  90deg ) translateZ( 25px ); }
.cubeSmall .left   { transform: rotateY( -90deg ) translateZ( 25px ); }
.cubeSmall .top    { transform: rotateX(  90deg ) translateZ( 25px ); }
.cubeSmall .bottom { transform: rotateX( -90deg ) translateZ( 25px ); }



