@import './cube';
@import './contact';

@font-face {
  font-family: "addefont";
  src: url("/assets/Helvetica-CondensedBold.woff") format("woff")
} 

$black: '#444';
$white: '#FAFAFA';

html, body {
	height: 100%;
	width: 100%;
	margin: 0;
	background: #FAFAFA;
	font-family: 'addefont', arial, sans-serif;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

body {
  display: flex;
  align-items: center;
}

* {
	box-sizing: border-box;
}

#app {


  width: 100%;
  padding: 1rem;
	display: flex;
	flex-flow: column nowrap;
  align-items: center;
	justify-content: space-around;
}







